
.content>.chatView {
    grid-column-start: 2;
    grid-column: span 4;
    margin-left: 15px;
    overflow: auto;
}
.content {
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: 320px auto auto auto auto;
    overflow: hidden;
    height: 100%;
}

.content>.report-list {
    grid-column: span 1;
}
.chat-Container{
    height: 100%;
}