.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.page-header {
  font-family: "JosefinSlabRegular";
  font-size: 32px;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: 0em;
  text-align: left;
}

input:focus, textarea:focus {
  outline: none;
}

input::placeholder {
  color: var(--quinary-color);
  @extend .regular-16;
}

input {
  @extend .regular-16;
  color: var(--quinary-color);
}

.popup-header {
  font-family: "JosefinSlabRegular";
  font-size: 22px;
  font-weight: 400;
  line-height: 22px;
}

.section-header {
  font-family: "JosefinSlabRegular";
  font-size: 18px;
  font-weight: 400;
  line-height: 18px;
}

.text-bold {
  font-family: 'JosefinSlabSemiBold';
  font-size: 22px;
  line-height: 20px;
}

.text-bold-small {
  font-family: 'JosefinSlabSemiBold';
  font-size: 18px;
}

.section-description {
  font-family: Josefin Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
}

.align-center {
  display: flex;
  align-items: center;
}

.gap-10 {
  gap: 10px;
}

.font-600 {
  font-weight: 600 !important;
}

.font-700 {
  font-weight: 700;
}

.App-link {
  color: #61dafb;
}

.justify-center {
  display: flex;
  justify-content: center;
}

.justify-spacebetween {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.profile-image {
  width: 48px;
  height: 48px;
  box-shadow: 2px 2px 4px 0px #00000033;
  border-radius: 50%;
  object-fit: cover;
}

/* josefin sns 400 with 18px size */
.regular-18 {
  font-family: var(--app-regular-font) !important;
  line-height: 20px !important;
  font-size: var(--heading3) !important;
}

/* josefin sns 400 with 22px size */
.regular-22 {
  font-family: var(--app-regular-font) !important;
  line-height: 20px !important;
  font-size: var(--heading5) !important;
}

/* josefin sans 600 with 18px size */
.semiBold-18 {
  font-family: 'JosefinSansSemiBold';
  line-height: 18px;
  font-size: var(--heading3);
}

/* josefin sans 600 with 16px size */
.semiBold-16 {
  font-family: 'JosefinSansSemiBold' !important;
  line-height: 20px !important;
  font-size: var(--heading2) !important;
}

/* josefin sans 600 with 22px size */
.semiBold-22 {
  font-family: 'JosefinSansSemiBold' !important;
  line-height: 22px !important;
  font-size: var(--heading5) !important;
}

.medium-18 {
  font-family: 'JosefinSansMedium' !important;
  font-size: var(--heading3) !important;
}

.lite-color {
  color: var(--quinary-color);
}

.regular-14 {
  font-family: var(--app-regular-font) !important;
  font-size: var(--heading1) !important;
  line-height: 14px !important;
}

/* josefin sans 400 with 16px size */
.regular-16 {
  font-family: var(--app-regular-font) !important;
  line-height: 20px !important;
  font-size: var(--heading2) !important;
}

.font-color {
  color: var(--tertiary-color);
}

.link_text {
  color: var(--link-color);
  text-decoration: underline;
  cursor: pointer;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.model-popup-preview {
  width: 1000px;
  height: 100px;
}

.main_content {
  background-color: var(--quaternary-color) !important;
  border: {
      radius: 25px !important;
  };
}

.no_padding {
  padding: 0 !important;
}

.dialouge-content-container {
    background-color: var(--quaternary-color) !important;
    border-radius: 0 0 10px 10px !important;
}

.primary_text_color {
  color: var(--primary-color);
}

.quinary_text_color {
  color: var(--quinary-color);
}

.attachment_label {
  text-decoration: underline;
  color: var(--primary-color);
  font-size: 14px;
  cursor: pointer;
}
