@import '../../../../App.scss';

.header {
    &-style {
        width: 100%;
        text-align: center;
        margin: 24px 0 30px 0 !important;
    }
}

.user_profile {
    padding: 0 14px 30px 14px;
    display: flex;
    flex-direction: column;
    gap: 12px;

    .profile_pic {
        width: 120px;
        height: 120px;
    }
}

.pin_name {
    @extend .regular-18;
    color: var(--quinary-color);
}

.chip_text {
    @extend .regular-14;
    line-height: 14px !important;
}

.popularity {
    &_count {
        @extend .regular-22;
        @extend .font-color;
    }
    &_text {
        @extend .medium-18;
        color: var(--quinary-color)
    }
}

.details {
    &_text {
        @extend .regular-14;
        line-height: 20px;
        color: var(--quinary-color);
    }

    &_value {
        @extend .regular-16;
        @extend .font-color;
        line-height: 20px;
    }
}
