@import '../../../../App.scss';

.profile {
    height: 75px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--secondary-color);
    box-shadow: 0px 4px 4px 0px rgba(30, 30, 30, 0.15);
    background-color: var(--secondary-color);
    padding: 0 15px;
    border-bottom: 0.5px solid var(--quinary-color);
    position: sticky;
}

p {
    margin: 0;
}

.three_dots {
    cursor: pointer;
}
.chatContent {
    border: 0.5px solid var(--quinary-color);
    border-radius: 25px;
    overflow: hidden;
    height: calc(100% - 5px);
}
.chatWindow {
    background-color: var(--senary-color);
    padding: 15px;
    display: flex;
    align-items: center;
    gap: 15px;
    height: calc(200% - 310px)

}
.smiley {
    position: absolute;
    left: 20px;
}
.arrow {
    padding-right: 20px;
}

.input-container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    flex-grow: 1;

}

.inputBox {
    height: 20px;
    border: 1px solid var(--quinary-color);
    border-radius: 25px;
    flex-grow: 1;
    padding: 10px 20px 10px 50px;
    line-height: 20px;
    font-family: 'josefinSansRegular';
    color: var(--tertiary-color);
    font-size: var(--heading3);
    overflow: hidden;
    resize: none;
    margin-bottom: 2px;
}
.profileIcon {
    width: 42px;
    height: 42px;
    border-radius: 50%;
    box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.2);
    object-fit: cover;
}
.profileInfo {
    display: flex;
    align-items: center;
    gap: 10px;
}
.message-layout{
    background-color: var(--senary-color);
    height: 100%;
}
