.report-list-profile {
    width: 42px;
    height: 42px;
    border-radius: 50%;
    // padding-bottom: 5px;
    object-fit: cover;
}

.report-list {
    padding: 0;
    // height: Fixed (840px);
    top: 164px;
    left: 120px;
    border-radius: 25px;
    border: 0.5px;
    // gap: 15px;
    background-color: var(--secondary-color);
    display: flex;
    flex-direction: column;
    overflow: {
        x: hidden;
        y: auto;
    };
    height: 100%;
}

.report-list>.search {
    padding: {
        top: 20px;
        left: 12px;
        right: 12px;
        bottom: 0
    };
    .lensIcon {
        top: 32px;
        left: 20px;
    }
    position: sticky;
    top: 0;
    background-color: var(--secondary-color);
    .closeIcon {
        right: 20px;
        top: 28px;
    }
}

.list-items {
    display: flex;
    align-items: center;
    justify-content: space-between;
    // padding-bottom: 5px;
    height: 65px;
    border-bottom: 1px solid var(--line-border-color);
    margin: 0 12px;
}

.query-container {
    padding-left: 10px;
    display: flex;
    flex-direction: column;
    gap: 4px;
    align-items: flex-start;
}

.query-label {
    font-family: "JosefinSlabSemiBold";
    font-size: 14px;
    color: var(--quinary-color);
}

.list-cell {
    cursor: pointer;
    &:hover:not(.highlight){
        background-color: var(--hover-color);
    };

    &:last-child {
        .list-items {
            border-bottom: none;
        }
    }
}

.list {
    &-content {
        display: flex;
        align-items: center;
    }
}

.highlight {
    background-color: var(--senary-color);
}