.search-container {
    height: 40px;
    border: solid 1px var(--quinary-color);
    border-radius: 15px;
    width: calc(100% - 75px);
    padding: {
        top: 0;
        left: 35px;
        bottom: 0;
        right: 40px
    };
}

.search {
    position: relative;
}

.lensIcon {
    top: 12px;
    position: absolute;
    left: 10px;
}

.closeIcon {
    position: absolute;
    top: 12px;
    right: 10px;
    color: var(--quinary-color);
    cursor: pointer;
}