.moreOptions{
    position: relative;
    border: solid 1px var(--quinary-color);
    border-radius: 10px;
    padding:0;
}
.list-Container {
    list-style-type: none;
    padding: 0;
    margin: 0;  
}
.list-Container li {

    cursor: pointer;
    // margin-bottom: 5px; 
    // margin-bottom: 10px; 
    font-size: var(--heading2);
    color: var(--primary-color);
    padding: 10px;
}
.list-item {
    margin: 0;
}

li:hover {
    // background-color: var(--hover-color);
    background-color: var(--tertiary-color);
    color: var(--secondary-color);
    border-radius: 10px;
}

.list-Container li:not(:last-child) {
    border-bottom: 1px solid var(--line-border-color);
   //padding-bottom: 5px; 
}

.popover_content {
    border-radius: 10px !important;
}

